export default {
    data () {
        return {
            // ards ustomizing
            cards: [
                {
                    topNote: '  ',
                    topNoteHover: '  ?',
                    productTitle: '',
                    productTitleDesc: ' -',
                    bonusFirst: '<span class="text-bold">10</span> <br>',
                    bonusSecond: '  ',
                    productQuantity: '0,5',
                    callAction: ' ?  , <button>.</button>',
                    productInfo: '    ',
                    productInactive: ', - ',
                    disabled: ''
                },
                {
                    topNote: '  ',
                    topNoteHover: '  ?',
                    productTitle: '',
                    productTitleDesc: ' ',
                    bonusFirst: '<span class="text-bold">40</span> <br>',
                    bonusSecond: '<span class="text-bold">2</span>   ',
                    productQuantity: '2',
                    callAction: ' ?  , <button>.</button>',
                    productInfo: '      ',
                    productInactive: ',   ',
                    disabled: ''
                },
                {
                    topNote: '  ',
                    topNoteHover: '  ?',
                    productTitle: '',
                    productTitleDesc: ' ',
                    bonusFirst: '<span class="text-bold">100</span> <br>',
                    bonusSecond: '<span class="text-bold">5</span>   <br>  ',
                    productQuantity: '5',
                    callAction: ' ?  , <button>.</button>',
                    productInfo: '      ',
                    productInactive: ',   ',
                    disabled: 'true'
                }
            ]
        }
    },
    methods:{
        // Cards selection switch method
        checkActivity(index, disabled) {
            if (disabled) return;
            let el = document.getElementById('card-' + index)
            el.classList.toggle('card-active');
            if (el.classList.contains('card-active')) {
                el.addEventListener('mouseenter', () => {
                    if (el.classList.contains('card-active')) {
                        el.classList.add('card-active-hover');
                    }
                });
            } else {
                el.classList.remove('card-active-hover');
            }
        }
    }
}